.styled-input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    width: 75px;
}

select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    width: 200px;
    cursor: pointer;
    outline: none;
  }
  
  /* 활성화된 상태 스타일 */
  select:focus {
    border-color: #2196F3;
    box-shadow: 0 0 5px rgba(33, 150, 243, 0.7);
  }
  
  /* 드롭다운 화살표 스타일링 */
  select::after {
    content: '\25BC'; /* 화살표 모양 */
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    font-size: 18px;
    color: #333;
    pointer-events: none;
  }

